:root{
    --main: #FFFFFF;
    --sub: #000035;
    --text: #000000;
}

.main{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: auto;
    width: 70%;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
    padding: 3rem;
    box-sizing: border-box;
}
.mainRight{
    width: 25%;
    /* border-radius: 10%; */
    background-color: var(--sub);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.mainRight img{
    /* border-radius: 10%; */
    margin: 20px 0 -20px 20px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;

}
.mainLeft{
    width: 25%;
}
.mainLeft button{
    width: 40%;
    background-color: var(--main);
    border: 2px solid var(--sub);
    color: var(--sub);
    font-weight: 400;
    border-radius: 20px;
    display: inline-flex;
    justify-content: space-evenly;
    align-items: center;
    height: 30px;
    margin: 10px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}
.icon{
    width: 20px;
}