:root{
    --main: #FFFFFF;
    --sub: #000035;
    --sub2: #000000;
}

.nav{
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    color: var(--main);
    padding: 0% 5%;
    background-color: var(--sub);
    margin-bottom: 30px;
}
.navR{
    display: flex;
    justify-content: space-evenly;
    width: 60%;
}
.navR p{
    background-color: var(--main);
    border-radius: 20px;
    padding: 0 1rem;
    color: var(--sub);
    font-weight: 600;
}